* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

img {
	max-width: 100%;
}

@font-face {
	font-family: "Ubuntu-R";
	src: url("../fonts/Ubuntu-R.woff2") format("woff2"), url("../fonts/Ubuntu-R.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: "Ubuntu-B";
	src: url("../fonts/Ubuntu-B.woff2") format("woff2"), url("../fonts/Ubuntu-B.woff") format("woff");
	font-weight: 700;
	font-style: normal;
}

@font-face {
	font-family: "Ubuntu-L";
	src: url("../fonts/Ubuntu-L.woff2") format("woff2"), url("../fonts/Ubuntu-L.woff") format("woff");
	font-weight: 200;
	font-style: normal;
}

#page {
	--baseDark: #fbd888;
	--baseLight: #c6ffdd;
	--baseText: #181f30;
	--grayblue: #3c5a78;
	--grey: #808080;
	--lightText: #26373f;
	--active: #008000;
	--negative: #800000;
	--maxWidth: 780px;
	--size: 14px;
	--ffr: 'Ubuntu-R';
	--ffl: 'Ubuntu-L';
	--ffb: 'Ubuntu-B';
}

.loader {
	position: fixed;
	z-index: 999;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	width: 100vw;
	background: linear-gradient(to bottom, #38486a, #181f30) 100% no-repeat;
	opacity: 1;
	transition: all 1000ms ease;
}

.loader--hide {
	opacity: 0;
}

.loader__svg {
	width: 200px;
	height: 200px;
}

.loader__svg-hex {
	fill: rgba(251, 216, 136, 0.8);
	animation: fadein 3.3s infinite;
	opacity: 0;
	transform-origin: 50% 50%;
}

.loader__svg-hex:nth-child(2) {
	animation-delay: .2s;
}

.loader__svg-hex:nth-child(3) {
	animation-delay: .4s;
}

.loader__svg-hex:nth-child(4) {
	animation-delay: .6s;
}

.loader__svg-hex:nth-child(5) {
	animation-delay: .8s;
}

.loader__svg-hex:nth-child(6) {
	animation-delay: 1s;
}

.loader__svg-hex:nth-child(7) {
	animation-delay: 1.2s;
}

@keyframes fadein {

	0%,
	100% {
		transform: scale(.001, .001);
		opacity: 0;
	}

	33%,
	66% {
		transform: scale(.9, .9);
		opacity: 1;
	}
}

.menu {
	position: fixed;
	width: 100%;
	height: 100vh;
	padding: 10px;
	top: 0;
	left: -100vw;
	bottom: 0;
	right: 0;
	transition: all 500ms ease;
	z-index: 1;
	background: linear-gradient(to bottom, var(--baseLight), var(--baseDark)) 100% no-repeat;
}

.menu--show {
	left: 0;
}

.menu__wrapper {
	max-width: var(--maxWidth);
	height: 100%;
	padding: 1vh;
	margin: 0 auto;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 8% 1fr 30%;
	grid-gap: 10px;
}

.icons {
	height: 100%;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.icons__input {
	display: block;
	padding: 0;
	height: 50px;
	width: 50px;
	outline: none;
	border: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	user-select: none;
	cursor: pointer;
}

.icons__theme {
	background: url(../images/sun.svg) center no-repeat;
	background-size: 50px;
}

.icons__theme--moon {
	background: url(../images/moon-eclipse.svg) center no-repeat;
	background-size: 50px;
}

.icons__help {
	fill: var(--lightText);
}

.options {
	height: 100%;
	display: flex;
}

.options__side {
	width: 50%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.choice {
	margin: 1rem auto;
	width: 15rem;
}

.choice__radio {
	margin: .5rem;
	position: relative;
}

.choice__input {
	position: absolute;
	opacity: 0;
}

.choice__label {
	display: block;
	width: 100%;
	height: 2.5rem;
	font-size: 1.5rem;
	color: #888;
	text-transform: uppercase;
	line-height: 2.5rem;
	margin-left: 3rem;
	cursor: pointer;
}

.choice__label:after {
	content: '';
	border-radius: 100%;
	display: block;
	width: 1.2rem;
	height: 1.2rem;
	position: absolute;
	left: 1rem;
	top: .65rem;
	cursor: pointer;
	transition: all 250ms ease;
}

.choice__input:checked+.choice__label {
	color: var(--lightText);
}

.choice__input:checked+.choice__label:after {
	background-color: var(--lightText);
}

.submit {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

.submit__input {
	color: var(--lightText);
	font-size: 1.5rem;
	height: 3.5rem;
	width: 15rem;
	border: 1px solid var(--grey);
	border-radius: 6vh;
	padding-left: 4vh;
	margin: 0 auto;
	background: transparent;
}

.submit__input:focus {
	outline: none;
}

.submit__button {
	display: block;
	outline: none;
	height: 3.5rem;
	width: 15rem;
	font-size: 1.5em;
	line-height: 3.5rem;
	margin: 0 auto;
	text-transform: uppercase;
	font-family: var(--ffb);
	color: var(--lightText);
	border: 1px solid var(--baseText);
	background: transparent;
	border-radius: 1.75rem;
	cursor: pointer;
}

.submit__button:hover {
	background: var(--baseLight)
}

.modal {
	position: fixed;
	width: 100%;
	height: 100%;
	top: -100vh;
	left: 0;
	z-index: 99;
	transition: all 500ms ease;
	background: linear-gradient(to bottom, var(--baseLight), var(--baseDark)) 100% no-repeat;
	display: flex;
	justify-content: center;
	align-items: center;
}

.modal--show {
	top: 0
}

.modal__content {
	font-size: 1.5rem;
	color: var(--lightText);
	background-color: transparent;
	padding: 2em 1em;
	margin: 0 auto;
	max-width: var(--maxWidth);
	border: 1px solid var(--grey);
	border-radius: .5em;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.modal__title,
.modal__text {
	line-height: 1.35em;
	text-align: center;
	margin-bottom: 5vh;
}

.modal__text {
	text-align: left
}

.modal__data {
	line-height: 1.5em;
	font-size: 4rem;
	color: var(--lightText);
	text-align: center;
	margin-bottom: 5vh;
}

.modal__close {
	height: 3.5rem;
	width: 15rem;
	font-size: 1.5em;
	text-transform: uppercase;
	text-align: center;
	line-height: 3.5rem;
	margin: auto;
	border: 1px solid var(--lightText);
	border-radius: 1.75rem;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}

.modal__close:hover {
	background: var(--baseLight)
}

.counter {
	height: 100%;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.counter__icons {
	display: block;
	height: 100%;
	outline: none;
	border: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	user-select: none;
	fill: var(--lightText)
}

.counter__icon-back {
	fill: var(--lightText);
	height: 50px;
	width: 50px;
	cursor: pointer;
}

.counter__icon {
	display: none;
	height: 100%;
	outline: none;
	border: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	user-select: none;
	fill: var(--lightText);
	height: 50px;
	width: 50px;
}

.counter__icon--show {
	display: block
}

.counter__persent {
	height: 100%;
	display: flex;
	align-items: center;
	font-size: calc( var(--size) * 2 );
	color: var(--lightText);
}

.counter__persent-text {
	display: block;
}

__theme {
	background: url(../images/sun.svg) center no-repeat;
	background-size: 50px
}

__theme--moon {
	background: url(../images/moon-eclipse.svg) center no-repeat;
	background-size: 50px
}

__help {
	fill: var(--lightText)
}

.ask {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center
}

.ask__words {
	flex-grow: 2;
	display: flex;
	justify-content: center;
	align-items: center
}

.ask__word {
	width: 100%;
	text-align: center;
	font-size: calc(var(--size) * 3);
	text-transform: lowercase;
	text-shadow: 2px 3px 4px rgba(0, 0, 0, 0.5);
	display: block;
	color: var(--lightText)
}

.media {
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center
}

.progress {
	position: relative;
	width: 90%;
	height: 3%;
	background: none;
	border: 1px solid var(--lightText)
}

.progress-bar {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	background: none
}

.audio__icon {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 120px;
	width: 120px;
	outline: none;
	border: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	user-select: none;
	border-radius: 50%;
	cursor: pointer
}

.audio__icon-svg {
	display: block;
	width: 100px;
	fill: var(--lightText)
}

.version {
	width: 100%;
	height: 100%;
	padding: 1rem 0;
	display: grid;
	justify-items: center;
	align-items: center;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	grid-gap: 1rem
}

.version__card {
	width: 100%;
	height: 100%
}

.version__button {
	font-size: calc(var(--size) * 1.5);
	text-transform: lowercase;
	color: var(--lightText);
	text-align: center;
	cursor: pointer;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: .5rem;
	border: 1px solid var(--lightText);
	transition: .3s
}

.win {
	background: #50ff50;
	color: #181f30
}

.lose {
	background: #ff5050;
	color: #181f30
}

#page {
	font-family: var(--ffr), sans-serif;
	font-size: var(--size)
}

.page {
	background: linear-gradient(to bottom, var(--baseLight), var(--baseDark)) 100% no-repeat;
	color: var(--baseText)
}

.main {
	position: relative;
	width: 100%;
	height: 100vh;
	padding: 10px;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: linear-gradient(to bottom, var(--baseLight), var(--baseDark)) 100% no-repeat
}

.main__wrapper {
	max-width: var(--maxWidth);
	height: 100%;
	padding: 1vh;
	margin: 0 auto;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 8% 1fr 30%;
	grid-gap: 10px
}

h2 {
	text-align: center;
	font-size: 1.25rem;
	line-height: 1.25rem;
	margin: .5rem 0
}

.show {
	display: block
}

.hidden {
	opacity: 0;
	transition: .3s
}